
#cat-content-left{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

#sorter-left{
    display: flex;
}

#equipHighestDensityCats{
    background: rgb(110, 110, 193);
    border: solid 2px rgb(29, 29, 40);
    border-radius: 5px;
    width: 15vw;
    margin-right: 2vw !important;
}

.mainSorting{
    background: rgb(90, 174, 90) !important;
}

.nonSorting{
    background: #883b2d!important;
}

.hideDisplay{
    filter: opacity(0);
}

.cat-sorting-button{
    width: 5vw;
    height: 2vh;
    background: rgb(143, 73, 30);

    display: flex;
    justify-content: center;
    align-items: center;
    border: solid black 1px;
    border-radius: 5px;

}

#cat-inventory-sorter{
    display: flex;
    width: 65vw;
    height: 3vh;
    background: #997e5a;

    border: solid 2px black;
    border-radius: 5px;
    position: relative;
    bottom: 1vh;

    justify-content: space-between;
    align-items: center;
}

#cat-inventory-sorter h3{
    font-size: 0.75vw;
    margin: 0;
    margin-left: 2vw;
    margin-top: 0.25vh;
    padding: 0;
}

#cat-inventory{

    width: 65vw;
    background: #997e5a;
    height: 78vh;

    border-radius: 15px;
    border: solid black 2px;

    overflow-y: scroll;
    overflow-x: hidden;

    scrollbar-color: rgb(134, 94, 62) rgb(179, 149, 111);
  scrollbar-width: thin;


}

.currently-selected-cat{
    border: rgb(116, 8, 8) solid 3px !important;
}

.currently-equipped-cat{
    background: rgba(99, 175, 99, 0.397) !important;
}

#cat-display{

    width: 25vw;
    background: rgb(126, 102, 72);
    height: 82.5vh;

    border-radius: 15px;
    border: solid black 2px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

#cat-content-container{
    display: flex;
    align-items: end;
    justify-content: space-evenly;
    height: 85vh;
}

.inventory-row{
    display: flex;
    width: 65vw;
    justify-content: space-evenly;
    align-items: center;
}

.inventory-tile{
    border: solid black 3px;
    width: 3vw;
    height: 3vw;
    padding: 0.5vw;

    font-size: 1vw;

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;

    margin: 1vh;

    background: rgb(187, 158, 119);
    position: relative; /* Set the container to relative so the absolute positioning is relative to it */
  display: inline-block;
}

.inventory-tile img{
    width: 3vw;
    height: 3vw;
}

.inventory-tile h3{
    position: absolute;
    left: 0.05vw;
    top: -0.9vw;
    font-size: 0.75vw;
}

#equip-cat{
    width: 20vw;
    height: 5vh;
    background: rgb(107, 233, 107);
    border-radius: 5px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 1vw;
    border: solid rgb(6, 53, 6) 3px; 
}

#unequip-cat{
    width: 20vw;
    height: 5vh;
    color: rgb(172, 136, 136);
    background: rgb(128, 38, 38);
    border-radius: 5px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 1vw;
    border: solid rgb(99, 10, 10) 3px;
}

#cat-display img{
    width: 20vw;
    height: 20vw;
}

#cat-display h1{
    font-size: 2vw;
}

#cat-display h2{
    font-size: 1.5vw;
}

